
.main-post{
    width: 100%;
    max-width: 930px;
    margin: 0 auto;
    border: 2px solid rgb(206, 203, 203);
    margin-top: 20px;
    border-radius: 20px;
    margin-bottom: 15px;
}
.header-post{
    padding-left: 5px;
}
.titulo-post{
    font-size: 30px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.autor-post{
    font-size: 15px;
    letter-spacing: 2px;
}

.container-foto{
    overflow: hidden;
}
.foto-post{
    height: 500px;
    width: 100%;
    transition: all ease-in-out 0.30s;
    cursor: pointer;
}
.foto-post:hover{
    transform: scale(1.3);
    transition: all ease-in-out 0.30s;
}
.footer-post{
    width: 100%;
    border-bottom-left-radius: 17px;
    border-bottom-right-radius: 17px;
    text-align: center;
    background-color: rgb(226, 216, 216);
    font-size: 18px;
    font-family: monospace;
    letter-spacing: 2px;
}