.container{
    display: flex;
    flex-direction: column;
    max-width: 930px;
    width: 100%;
    margin:  140px auto;
    padding: 40px 40px;
    align-items: center;
    /* border: 1px solid red; */
}
.container label{
    width: 100%;
    max-width: 500px;
    font-size: 20px;
    letter-spacing: 3px;
    
}

.container input{
    width: 100%;
    max-width: 500px;
    border: 1px solid black;
    border-radius: 5px;
    padding: 5px 10px;
    margin: 3px 0;
}

.container button{
    width: 100%;
    max-width: 500px;
    border: 1px solid white;
    font-size: 19px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    background-color: black;
    padding: 5px 0;
    color: white;
    text-transform: uppercase;
    cursor: pointer;
}
.container button:hover{
    color: black;
    background-color: white;
    border-color: black;
}
