.container-form{
    display: flex;
    flex-direction: column;
    max-width: 930px;
    width: 100%;
    margin:  160px auto;
    padding: 40px 40px;
    align-items: center;
    /* border: 1px solid red; */
}

.container-form label{
    font-size: 20px;
    letter-spacing: 2px;
}

.container-form input{
    border: 1px solid black;
    border-radius: 30px;
    padding: 5px 10px;
}

.container-form button{
    width: 40%;
    border: 1px solid white;
    font-size: 15px;
    border-radius: 60px;
    background-color: black;
    color: white;
    text-transform: uppercase;
    font-weight: bold;
    cursor: pointer;
}
.container-form button:hover{
    color: black;
    background-color: white;
    border-color: black;
}
.container-form .register{
    margin-top: 10px;
    color: cadetblue;
    text-decoration: none;
}
.container-form a:hover{
    color: tomato
}

.links{
    display: flex;
    flex-direction:column ;
    justify-content: center;
    text-align: center;
    align-items: center;
}
.politica_senha{
    padding-top: 3px;
    color: brown;
    cursor: pointer;
}