#form-post{
    display: flex;
    flex-direction: column;
    max-width: 930px;
    width: 100%;
    margin:  160px auto;
    padding: 40px 40px;
    align-items: center;
}

#form-post label{
    font-size: 20px;
    letter-spacing: 3px;
    align-self: start;
    padding-left: 10px;
}
#form-post input{
    width: 100%;
    border: 1px solid black;
    border-radius: 10px;
    padding: 5px 10px;
}
#form-post button{
    width: 60%;
    border: 1px solid white;
    font-size: 15px;
    border-radius: 60px;
    padding-top: 5px;
    padding-bottom: 5px;
    background-color: black;
    color: white;
    text-transform: uppercase;
    font-weight: bold;
    cursor: pointer;
}
#form-post button:hover{
    color: black;
    background-color: white;
    border-color: black;
}

#form-post textarea{
    width: 100%;
    height: 200px;
    margin-bottom: 10px;
    border: 1px solid black;
    border-radius: 10px;
    padding: 5px 10px;
}