.main-header{
    background-color: black;
    height: 72px;
}

.header-content{
    width: 100%;
    max-width: 930px;
    height: 72px;
    margin: 0 auto;
    padding: 0 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header-content a{
    text-decoration: none;
    color: white;
    font-weight: bold;
    font-size: 25px;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.header-content a:hover{
    color: gold;
}